body {
  background: #fff;
  line-height: 1.4;
  font-size: 16px;
  min-width: 320px;
  font-family: 'proximanova-regular', Arial, sans-serif;
  color: #000;
}

body.no-scroll {
  overflow: hidden;
}

.no-touch body.no-scroll {
  margin-right: 17px;
}

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit;
}

.img {
  width: 40px;
  height: 40px;
}

.overflow-hidden {
  overflow: hidden;
}
